/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.dv-container .pdfobject-container {
	max-height: 80vh;
	height: 700px;
	min-height: 400px;
	border: 1px solid #ccc;
}

.dv-container .dv-btn-container {
	margin: 30px auto 0;
	text-align: center;

}
.dv-container .dv-btn-container a.wp-block-file__button{
	padding: 10px;
	background: blue;
	color: #fff;
	border-radius: 5px;
}

/* General styling for code blocks */
.dv-container pre {
	background-color: #f5f5f5;
	border: 1px solid #ddd;
	border-radius: 5px;
	padding: 15px;
	overflow: auto;
	font-size: 14px;
	line-height: 1.6;
	font-family: 'Courier New', Courier, monospace;
	white-space: pre-wrap; /* Enable line wrap for long code */
}

/* Styling for inline code */
.dv-container code {
	background-color: #f5f5f5;
	border-radius: 3px;
	padding: 2px 4px;
	font-size: 14px;
	font-family: 'Courier New', Courier, monospace;
	color: #d14;
}

/* Additional styling for specific elements */
.dv-container pre code {
	background-color: transparent; /* Remove background for code inside pre */
	padding: 0; /* Remove padding for code inside pre */
}

.dv-container pre::-webkit-scrollbar {
	width: 8px;
	height: 8px;
}

.dv-container pre::-webkit-scrollbar-thumb {
	background: #888;
	border-radius: 4px;
}

.dv-container pre::-webkit-scrollbar-thumb:hover {
	background: #555;
}

/* Syntax highlighting (optional) */
/* Add your syntax highlighting styles here */
.token.comment, .token.block-comment, .token.prolog, .token.doctype, .token.cdata {
	color: #999988;
	font-style: italic;
}
.token.punctuation {
	color: #999;
}
.token.namespace {
	opacity: .7;
}
.token.property, .token.tag, .token.boolean, .token.number, .token.constant, .token.symbol, .token.deleted {
	color: #905;
}
.token.selector, .token.attr-name, .token.string, .token.char, .token.builtin, .token.inserted {
	color: #690;
}
.token.operator, .token.entity, .token.url, .language-css .token.string, .style .token.string {
	color: #a67f59;
	background: hsla(0, 0%, 100%, .5);
}
.token.atrule, .token.attr-value, .token.keyword {
	color: #07a;
}
.token.function {
	color: #DD4A68;
}
.token.regex, .token.important, .token.variable {
	color: #e90;
}
.token.important, .token.bold {
	font-weight: bold;
}
.token.italic {
	font-style: italic;
}
.token.entity {
	cursor: help;
}
